



















import { Component, Vue } from 'vue-property-decorator';
// import { mapState } from 'vuex'
import { Header, Aside } from '@/components';

@Component({
  components: {
    YHeader: Header,
    YAside: Aside,
  },
  name: 'home',
})
export default class Home extends Vue {

  get menuState(): boolean {
    return this.$store.state.home.menuState;
  }

  private data() {
    return {
      data: null,
      menuOpen: false,
      headerOptions: {
        '/music-article': {
          title: '乐文',
          sub_title: 'MUSIC　ARTICLE',
        },
        '/huntings': {
          title: '猎乐',
          sub_title: 'HUNTINGS',
        },
        '/melody': {
          title: '单曲',
          sub_title: 'MELODY',
        },
        '/indie-artist': {
          title: '乐人',
          sub_title: 'INDIE ARTIST',
        },
        '/music-radio': {
          title: '电台',
          sub_title: 'MUSIC　RADIO',
        },
      },
    };
  }

  private openMenu() {
    this.$store.commit('home/toggleMenuState', true);
  }

  private closeMenu() {
    this.$store.commit('home/toggleMenuState', false);
  }

}
